#txt{
  border-radius: 20px;
  background-color: rgb(255, 255, 255, .1);
  color: beige;
}

#i{
  background-color: rgb(255, 255, 255, .1);
  border-radius: 25px;
  padding: 10px;
  margin-left: 12px;
}

.sub-main{
  background-color: rgb(255, 255, 255, .1);
  backdrop-filter: blur(13px);
  padding: 10px;
  border-radius: 10px;
  justify-content: center;
}

#here{
  font-family: 'Irish Grover', cursive;
font-family: 'Mochiy Pop One', sans-serif;
}

@media only screen and (max-width: 600px) {
  #spanning{
   margin-left: 10%;
   margin-right: 10%;
  }
  #here i{
    visibility: hidden;
  }
}